.NavigationItem{
    margin : 10px 0;
    display : block;
    box-sizing: border-box;
    width : 100%;
}

.NavigationItem a{
    color : #8F5C2C;
    text-decoration: none;
    width : 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItem a:active,
.NavigationItems a:hover,
.NavigationItem a.active{
    color : #40A4C8;
}


@media (min-width : 500px){

    .NavigationItem{
        margin : 0;
        display : flex;
        width : auto;
        height : 100%;
        align-items: center;
    }
    
    .NavigationItem a{
        color : white;
        height : 100%;
        padding : 16px 10px;
        border-bottom: 4px solid transparent;
    }
    
    .NavigationItem a:active,
    .NavigationItems a:hover,
    .NavigationItem a.active{
        background-color: #8F5C2C;
        border-bottom: 4px solid #40A4C8;
        color : white;
    }
    
}